.signin-form{
    display: inline-block;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    padding:20px;
    width:40%;
}
.signin-form h2{
    font-size: 1.25rem;
    font-weight: 200;
    margin-bottom: 30px;
}
.signin-form input,.signin-form select{
    width:80%;
    margin-bottom:30px;
    padding:10px;
    border-radius: 14px;
    border:1px solid #dfdfdf;
    box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
    font-size: 1.1rem;
}
.signin-form label{
    display: block;
    font-size: 0.8rem;
    position:relative;
    right:-10%;
}
.signin-form .submit-btn{
    width:auto;
    padding:5px 20px;
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
    cursor: pointer;
    border:1px solid #dfdfdf;
}
.signin-form .submit-btn:hover{
    box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.back-btn{
    position: absolute;
    top:20px;
    left:20px;
    width:auto;
    padding:5px 20px;
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
    cursor: pointer;
    border:1px solid #dfdfdf;
    color:black;
    border-radius: 1rem;
}
.back-btn:hover{
    background-color: #fff;
}
