html {
    scroll-behavior: smooth;
  }
*{
    box-sizing: border-box;
    margin:0;
    padding:0;
}
*,*:focus,*:hover{
    outline:none;
}
body{
    background-color: #e6e7ee;
    font-size: 1rem;
    font-weight: 300;
    line-height: 1.5;
    color: #31344b;
    margin: 0;
    font-family: 'Nunito Sans', sans-serif;
}
.container{
    margin:0 50px;
}
.divider{
    height:0.5px;
    background-color: rgb(163, 163, 163);
    /* width:100%;   */
    max-width: 100%;
    /* margin:10px; */
}
a{
    text-decoration:none;
}
.hero-section{
    width:100%;
    height:calc(100vh - 54px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    padding:10px 0;
}
.hero-section h1{
    font-weight: 200;
    text-shadow:  2px 2px 3px rgb(75, 75, 75);
    margin-bottom: 50px;
}
.hero-section .image-container{
    height:300px;
    width:auto;
}
.hero-section .image-container img{
    max-width:100%;
    /* width: 100%; */
    max-height:100%;
}
.neomorphism-effect{
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
}
.status-section {
    padding:30px 0 40px 0;
}
.status-form input{
    /* display: block; */
    width:40%;
    padding:10px;
    font-size:1.1rem;
    border-radius: 14px;
    background-color: #e6e7ee;
    /* border:1px solid  grey; */
    border:1px solid #e6e7ee;
    box-shadow: inset 3px 3px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.status-form input:focus{
    /* border: none; */
}
.status-form .submit-btn{
    /* position:relative;
    left:-50px; */
    width:auto;
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #b8b9be;
    padding:7px 15px;
    margin-left:5px;
    transition: all 0.3s ease;
    cursor: pointer;
    background-color: #e76247;
    border: 1px solid black;
    /* background-color: grey;
    color:#31344b; */
}
.status-form .submit-btn:hover{
    background-color: #fff;
}
footer{
    background-color: rgb(173, 173, 173);
    padding:20px 0;
}
.footer-links{
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}
.footer-links p{
    margin-bottom: 10px;
    color:black;
    text-decoration: underline;
}
ul li{
    list-style: none;
    cursor: pointer;
    padding:2px 0;
}
.footer-form input{
    padding:7px;
    font-size:0.9rem;
    border-radius: 14px;
    background-color: #e6e7ee;
    border:1px solid #e6e7ee;
    box-shadow: inset 3px 3px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.footer-form .submit-btn{
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #b8b9be;
    padding:7px 15px;
    margin-left:5px;
    transition: all 0.3s ease;
    cursor: pointer;
    background-color: #e76247;
    border: 1px solid black;
}
.footer-form .submit-btn:hover{
    background-color: #fff;
}
.tracking-details{
    display: inline-block;
    padding:20px;
    margin-top:20px;
}
