.dashboard{
    width:100%;
    height:100vh;
    overflow-y: auto;
    padding:1rem;
}
.dashboard h1{
    font-size: 1.4rem;
    font-weight: 300;
}
.dashboard .tabs{
    display: flex;
    align-items: center;
    justify-content: center;
    margin:1rem 0;
}
.dashboard .tabs .tab-btn{
    padding:0.75rem 3rem;
    box-shadow: 6px 6px 12px #b8b9be, -6px -6px 12px #ffffff;
    border:1px solid #D1D9E6;
    border-radius: 0.55rem;
    margin-left: 2rem;
    transition: all 0.2s ease;
    font-weight: 400;
    font-size: 1.2rem;
    cursor: pointer;
}
.dashboard .tabs .active{
    box-shadow:inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.dashboard .tabs .tab-btn:hover{
    box-shadow:inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.dashboard-back{
    box-shadow: 6px 6px 12px #b8b9be, -6px -6px 12px #ffffff;
    padding:0.5rem 1.8rem;
    position: absolute;
    top:20px;
    right:20px;
    border:1px solid #D1D9E6;
    border-radius: 0.55rem;
    transition: all 0.2s ease;
    cursor: pointer;
    font-size: 1.1rem;
}
.dashboard-back:hover{
    box-shadow:inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
}
.dashboard-content{
    width:auto;
    max-width: 80%;
    height:75%;
    margin:auto;
    border-radius: 0.5rem;
    box-shadow:inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
    display: flex;
    justify-content: center;
    padding:5px;
    overflow-y: auto;
}
table{
    width:100%;
    border-collapse: collapse;
}
table th{
    color:#e76247;
}
table tr {
    width:100%;
    display: table-row;
    border-bottom: 1px solid grey;
  }
  table th, table td {
    padding: 1rem;
    vertical-align: top;
    text-align: center;
  }
  .delete-btn{
      padding:0.4rem 1rem;
      border: none;
      border-radius: 0.9rem;
      background-color: rgb(255, 71, 71);
      color:#fff;
      cursor:pointer;
  }
  .add-btn{
    padding:0.4rem 2rem;
    border: none;
    border-radius: 0.9rem;
    background-color: rgb(80, 80, 255);
    color:#fff;
    cursor:pointer;
    margin-top:10px;
  }