nav{
    display: flex;
    justify-content:space-between;
    align-items: center;
}
nav .navbar-logo{
    font-size: 1.4rem;
    font-weight: 700;
}
nav .nav-link{
    font-size: 1.2rem;
    color:#31344b;
    cursor: pointer;
    transition: all 0.2s ease;
    margin-left:20px;
}
nav .nav-link:hover{
color:#e76247;
}
.login-btn{
    background-color: #e76247;
    color:#ffffff !important;
    border-radius: 0.8rem;
    padding:0.35rem 0.9rem;
    letter-spacing: 1px;
    /* margin-left:20px; */
    transition: all 0.2s ease;
}
.login-btn:hover{
    /* color:#e76247; */
    background-color: #31344b;

}
.navbar-container{
    box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
    padding:10px 0;
}